*
  box-sizing: border-box
  margin: 0
  padding: 0

body
  background-color: $color-bg
  overflow-x: hidden
  color: $color-content
  font-family: $font-family
  font-size: $font-size
  line-height: $font-lh
  
main
  display: flex
  flex-direction: column
  margin-top: 80px
  margin-bottom: 30px
  
  @media #{$tablet-width}
    margin: 0