.footer
  position: fixed
  bottom: 0
  left: 0
  right: 0
  display: flex
  justify-content: space-between
  align-items: center
  height: 30px
  color: $color-header
  font-size: $font-size-footer
  letter-spacing: 1.8px
  background-color: $color-bg-header
  box-shadow: 0 -5px 15px $color-bg-header-shadow
  z-index: 100

  &__inner
    display: flex
    justify-content: space-between
    align-items: center
    margin: 0 30px
    width: 100%
    
  @media #{$tablet-width}
    position: static
    height: auto
  
    &__inner
      flex-direction: column
      text-align: center
      padding: 15px 0