/*Layouts*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: whitesmoke;
  overflow-x: hidden;
  color: black;
  font-family: Trebuchet MS, Helvetica, sans-serif;
  font-size: 18px;
  line-height: 1.4em;
}

main {
  display: flex;
  flex-direction: column;
  margin-top: 80px;
  margin-bottom: 30px;
}
@media only screen and (max-width: 1023px) {
  main {
    margin: 0;
  }
}

input {
  display: block;
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
  font-family: Trebuchet MS, Helvetica, sans-serif;
  font-size: 18px;
  font-style: italic;
  background-color: transparent;
  border: 2px solid black;
}
input:last-of-type {
  margin-bottom: 0;
}
input:disabled {
  opacity: 0.3;
}

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 250px;
  height: 60px;
  color: whitesmoke;
  font-family: Trebuchet MS, Helvetica, sans-serif;
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  line-height: 1;
  background-color: black;
  border: 2px solid black;
  cursor: pointer;
  transition: all 0.35s;
}
.button:hover {
  background-color: whitesmoke;
  color: black;
}
.button:disabled {
  opacity: 0.2;
  cursor: default;
}
.button:disabled:hover {
  color: whitesmoke;
  background-color: black;
}
.button--danger {
  background-color: #ca0000;
  border-color: #ca0000;
}
.button--danger:disabled:hover {
  color: whitesmoke;
  background-color: #ca0000;
}
.button:active {
  opacity: 0.6;
}

.error-indicator {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  font-size: 46px;
  line-height: 1.2em;
}
.error-indicator button {
  margin-top: 30px;
}

.section {
  position: relative;
  margin: 40px 0;
}
.section__inner {
  margin: 0 auto;
  padding: 0 15px;
  width: 1360px;
}
.section__heading {
  margin: 0;
  margin-bottom: 20px;
  font-family: Trebuchet MS, Helvetica, sans-serif;
  font-size: 28px;
  line-height: 1.4em;
  font-weight: 700;
  text-transform: uppercase;
}
@media only screen and (max-width: 1360px) {
  .section__inner {
    width: 100%;
  }
}
@media only screen and (max-width: 1023px) {
  .section__inner {
    width: 100%;
  }
}
@media only screen and (max-width: 768px) {
  .section {
    margin-bottom: 0;
  }
  .section__inner {
    padding: 0 10px;
  }
}

.page-404 {
  width: 100vw;
  height: 100vh;
  background-repeat: no-repeat;
  background-position: bottom center;
  background-size: contain;
  background-color: whitesmoke;
}
.page-404__inner {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.9);
}
.page-404__heading {
  font-size: 54px;
  line-height: 1.3em;
  font-weight: 700;
}
.page-404__button {
  display: block;
  padding: 10px;
  font-family: Trebuchet MS, Helvetica, sans-serif;
  font-size: 24px;
  line-height: 1.3em;
  background: none;
  border: 4px solid black;
  transition: all 0.35s;
  cursor: pointer;
}
.page-404__button:hover {
  color: rgba(255, 255, 255, 0.9);
  background: black;
}

.account__button {
  margin-top: 15px;
}
.account__submit-response {
  height: 25px;
  margin-top: 20px;
  color: #00ca00;
}
.account__submit-response--error {
  color: #ca0000;
}

.product-page__item {
  display: flex;
  justify-content: space-between;
  font-size: 18px;
}
.product-page__img {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 45%;
  background-color: white;
}
.product-page__img img {
  display: block;
  max-width: 80%;
  max-height: 400px;
  margin: auto;
}
.product-page__meta {
  display: flex;
  flex-direction: column;
  width: 50%;
  padding: 20px;
}
.product-page__model {
  margin-top: 10px;
  margin-bottom: 30px;
  font-family: Trebuchet MS, Helvetica, sans-serif;
  font-size: 50px;
  line-height: 1.6;
  font-weight: 400;
}
.product-page__price {
  margin: 30px 0;
  font-size: 24px;
  font-weight: 700;
}
.product-page__old-price {
  font-weight: 400;
  text-decoration: line-through;
}
.product-page__description {
  margin: 30px 0;
  line-height: 1.6;
}
@media only screen and (max-width: 768px) {
  .product-page__item {
    flex-direction: column;
  }
  .product-page__img {
    width: auto;
  }
  .product-page__meta {
    padding-top: 10px;
    width: auto;
  }
  .product-page__model {
    margin-bottom: 5px;
    font-size: 24px;
    line-height: 1.6;
    font-weight: 700;
  }
  .product-page__price {
    margin: 10px 0;
  }
  .product-page__description {
    margin-top: 10px;
  }
}

.cart-page__item {
  display: flex;
  flex-direction: row;
  font-size: 16px;
  line-height: 1.3em;
}
.cart-page__item div {
  display: flex;
  justify-content: center;
  padding: 5px;
  background-color: white;
  align-items: center;
}
.cart-page__number {
  width: 5%;
  border: 2px solid black;
  border-right-width: 0;
}
.cart-page__model {
  width: 20%;
  font-weight: 700;
  border: 2px solid black;
  border-right-width: 0;
}
.cart-page__img {
  width: 10%;
  border: 2px solid black;
  border-right-width: 0;
}
.cart-page__img img {
  display: block;
  width: 100%;
}
.cart-page__description {
  width: 40%;
  border: 2px solid black;
  border-right-width: 0;
}
.cart-page__count {
  width: 5%;
  border: 2px solid black;
  border-right-width: 0;
}
.cart-page__price {
  width: 7%;
  border: 2px solid black;
  border-right-width: 0;
}
.cart-page__value {
  justify-content: space-between;
  width: 13%;
  border: 2px solid black;
}
.cart-page__trash-icon {
  display: block;
  margin: 0 20px;
  width: 20px;
  height: 20px;
  background-color: transparent;
  border: none;
  transition: all 0.35s;
}
.cart-page__trash-icon:hover {
  cursor: pointer;
  opacity: 0.7;
}
.cart-page__count-icon {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  color: black;
  font-size: 14px;
  line-height: 1;
  font-weight: 700;
  background-color: transparent;
  border: 2px solid black;
  transition: all 0.35s;
}
.cart-page__count-icon:hover {
  cursor: pointer;
  opacity: 0.5;
}
.cart-page__sum {
  display: block;
  margin-top: 5px;
  text-align: right;
}
.cart-page__buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}
@media only screen and (max-width: 1023px) {
  .cart-page__buttons .button {
    margin: 0px 10px;
  }
}
@media only screen and (max-width: 768px) {
  .cart-page__item {
    flex-direction: column;
  }
  .cart-page__item div {
    width: auto;
    border-right-width: 1px;
  }
  .cart-page__number {
    width: 5%;
    border-width: 1px;
    border-bottom-width: 0;
  }
  .cart-page__model {
    width: 20%;
    font-weight: 700;
    border-width: 1px;
    border-bottom-width: 0;
  }
  .cart-page__img {
    width: 10%;
    border-width: 1px;
    border-bottom-width: 0;
  }
  .cart-page__img img {
    max-height: 150px;
    width: auto;
  }
  .cart-page__description {
    width: 40%;
    border-width: 1px;
    border-bottom-width: 0;
  }
  .cart-page__count {
    width: 5%;
    border-width: 1px;
    border-bottom-width: 0;
  }
  .cart-page__price {
    width: 7%;
    border-width: 1px;
    border-bottom-width: 0;
  }
  .cart-page__value {
    justify-content: space-between;
    width: 13%;
    border-width: 1px;
  }
  .cart-page__buttons {
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  .cart-page__buttons .button {
    margin-bottom: 10px;
  }
}

.order-page__items {
  margin: 0;
  margin-bottom: 40px;
  padding: 0;
}
.order-page__items li {
  display: block;
  font-size: 24px;
  font-weight: 700;
  line-height: 1.4em;
}
.order-page__account {
  margin-bottom: 30px;
}
.order-page__account input:invalid {
  border-color: #ca0000;
}
.order-page__buttons {
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
}
.order-page__count {
  display: block;
  font-size: 24px;
}
.order-page__sum {
  display: block;
  margin-top: 20px;
  font-size: 24px;
  font-weight: 700;
}
@media only screen and (max-width: 768px) {
  .order-page__buttons {
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  .order-page__buttons button {
    margin-bottom: 10px;
  }
}

