  cursor: pointer;
}
.user-menu__button:first-child {
  margin-left: 0;
}
.user-menu__button:hover {
  color: #009688;
}
.user-menu__name {
  color: #009688;
  text-decoration: none;
  transition: all 0.35s;
}
.user-menu__name:hover {
  color: white;
}
@media only screen and (max-width: 1360px) {
  .user-menu__button {
    margin-left: 10px;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9999;
}
.modal__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 30px;
  width: 480px;
  min-height: 380px;
  background-color: whitesmoke;
  box-shadow: 0px 0px 15px 5px rgba(0, 0, 0, 0.5);
}
.modal__inner--prompt {
  min-height: 240px;
}
.modal__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}
.modal__body {
  display: block;
  margin: 10px 0;
  text-align: center;
  line-height: 1.4em;
}
.modal__footer {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.modal__footer--prompt {
  flex-direction: row;
}
.modal__footer--prompt button:last-child {
  margin-left: 50px;
}
.modal__heading {
  font: Trebuchet MS, Helvetica, sans-serif;
  font-size: 28px;
  line-height: 1.4em;
  font-weight: 400;
  text-transform: uppercase;
}
.modal__close {
  font-size: 18px;
  font-weight: 700;
  text-transform: uppercase;
  transition: all 0.35s;
  cursor: pointer;
}
.modal__close:hover {
  opacity: 0.4;
}
.modal__submit-response {
  margin-bottom: 10px;
  height: 25px;
  line-height: 1em;
  color: #00ca00;
}
.modal__submit-response--error {
  color: #ca0000;
}
@media only screen and (max-width: 768px) {
  .modal__inner {
    padding: 20px 10px;
    width: 300px;
  }
  .modal__heading {
    font-size: 26px;
    text-transform: none;
  }
}

.spinner {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.25);
  z-index: 99;
}
.spinner--fixed {
  position: fixed;
  width: 100vw;
  height: 100vh;
}
.spinner__inner {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 128px;
  height: 128px;
}
.spinner--small .spinner__inner {
  width: 64px;
  height: 64px;
}
.spinner--small .spinner__circle {
  border-width: 2px;
}
.spinner__circle {
  box-sizing: border-box;
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  border: 4px solid black;
  border-radius: 50%;
  border-top: 4px solid transparent;
  border-bottom: 4px solid transparent;
  animation: rotate 1s infinite ease-in-out;
}
.spinner__circle--inner {
  right: 0;
  bottom: 0;
  margin: auto;
  width: 50%;
  height: 50%;
  border: 4px solid black;
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  animation: rotate-rev 1s infinite ease-in-out;
}

@keyframes rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes rotate-rev {
  from {
    transform: rotate(360deg);
  }
}
.slider {
  position: relative;
}
.slider .slide {
  height: calc(100vh - 100px);
  width: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  outline: none;
}
.slider .slick-dots {
  position: absolute;
  bottom: 50px;
}
.slider .slick-dots .slick-active button {
  border: 1px solid white;
}
.slider .slick-dots li {
  width: 12px;
  height: 12px;
}
.slider .slick-dots button {
  display: block;
  width: 10px;
  height: 10px;
  border: 1px solid rgba(255, 255, 255, 0.7);
  border-radius: 50%;
  transition: 0.25s all;
}
.slider .slick-dots button:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  display: block;
  width: 10px;
  height: 10px;
  background: #000;
  border-radius: 50%;
  transition: 0.25s all;
}

