.footer__inner .cart-button {
  display: none;
}

.cart-button {
  position: relative;
  margin-left: 20px;
  text-decoration: none;
  background: transparent;
  border: none;
}
.cart-button__wrapper {
  display: flex;
  flex-direction: column;
}
.cart-button__icon {
  width: 40px;
  height: 40px;
  fill: white;
  transition: all 0.35s;
}
.cart-button__sum {
  margin-top: 5px;
  text-align: right;
  color: white;
  font-size: 16px;
  line-height: 1em;
  font-family: Trebuchet MS, Helvetica, sans-serif;
  font-weight: 700;
  transition: all 0.35s;
}
.cart-button:hover {
  cursor: pointer;
}
.cart-button:hover .cart-button__icon {
  fill: #009688;
}
.cart-button:hover .cart-button__count {
  color: #009688;
  background-color: white;
}
.cart-button:hover .cart-button__sum {
  color: #009688;
}
.cart-button__count {
  position: absolute;
  top: 0px;
  right: -5px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  color: white;
  font-size: 12px;
  line-height: 1em;
  font-family: Trebuchet MS, Helvetica, sans-serif;
  font-weight: 700;
  background-color: #009688;
  border-radius: 50%;
  transition: all 0.35s;
}
@media only screen and (max-width: 1360px) {
  .cart-button {
    margin-left: 10px;
  }
  .cart-button__icon {
    width: 30px;
    height: 30px;
  }
  .cart-button__sum {
    margin-top: 2px;
    font-size: 14px;
  }
  .cart-button__count {
    top: -2px;
    width: 15px;
    height: 15px;
    font-size: 8px;
  }
}