.modal
  position: fixed
  top: 0
  left: 0
  display: flex
  justify-content: center
  align-items: center
  width: 100%
  height: 100vh
  background-color: $color-bg-modal
  z-index: 9999

  &__inner
    position: relative
    display: flex
    flex-direction: column
    justify-content: space-between
    padding: 30px
    width: 480px
    min-height: 380px
    background-color: $color-bg
    box-shadow: 0px 0px 15px 5px $color-bg-modal

    &--prompt
      min-height: 240px

  &__header
    display: flex
    justify-content: space-between
    align-items: center
    margin-bottom: 10px

  &__body
    display: block
    margin: 10px 0
    text-align: center
    line-height: $font-lh

  &__footer
    display: flex
    flex-direction: column
    align-items: center

    &--prompt
      flex-direction: row

      & button:last-child
        margin-left: 50px

  &__heading
    font: $font-family
    font-size: 28px
    line-height: $font-lh
    font-weight: 400
    text-transform: uppercase

  &__close
    font-size: 18px
    font-weight: 700
    text-transform: uppercase
    transition: $transition
    cursor: pointer

    &:hover
      opacity: 0.4

  &__submit-response
    margin-bottom: 10px
    height: 25px
    line-height: 1em
    color: $color-success
    &--error
      color: $color-error
      
  @media #{$mobile-width}
   
    &__inner
      padding: 20px 10px
      width: 300px
    
    &__heading
      font-size: 26px
      text-transform: none
